import { Component } from '@angular/core';

@Component({
  selector: 'overview',
  templateUrl: '../templates/overview.html'
})
export class OverviewComponent {
	public careers:Array<{career}> = [
		{career: "Ingeniero de Sistemas e Informática"},
		{career: "Especialista en Mercados de Energía"}
	];
	public about:string = "Mi nombre es Neiro Andres Torres Castro, Ingeniero en Sistemas e Informática y Especialista en Mercados de Energía de la Universidad Nacional de Colombia, sede Medellín. Amante de la programación, especialmente el desarrollo web enfocado al Front-end.";
	public university:string="Universidad Nacional de Colombia"
}



