import { Component } from '@angular/core';

@Component({
  selector: 'about',
  templateUrl: '../templates/about.html'
})

export class AboutComponent {
	public name:string="Portafolio Neiro Torres"
	public title:string = "Construido en: ";
	public framework:string = "Angular 4";
	
	public addTools:Array<{tool,version}> = [{
			tool:"Angular CLI: ",
			version:"4.0.0"
		},
		{
			tool:"Material Design: ",
			version:"Lite"
		},
		{
			tool:"NodeJS: ",
			version:"6.0.60"
		},
		{
			tool:"Typescript: ",
			version:"2.2.0"
		}]
}



